
.card-input-outline{
  width:100%;
  border-bottom:1px dashed #dbdbdb;
  float:left;
  padding-bottom: 5px;
  margin-bottom: 5px;
  line-height: 35px;
}
.card-input-outline > .right-font{
  font-size:12px;
  margin-left:5px;
  height:35px;width:100px;
  float:left;line-height:35px;
  margin-top:1px;
}
.card-input{
  border: 1px solid #dbdbdb;
  width: 170px;
  line-height: 35px;
  overflow: hidden;
  float: left;
  vertical-align:center;
}
.card-input em{
  display: block;
  height: 35px;
  width: 60px;
  float: left;
  color: #7A7979;
  border-right: 1px solid #dbdbdb;
  text-align: center;
}
.card-input em.right{
  width: 30px;
  float: right;
  color: #7A7979;
  border-left: 1px solid #dbdbdb;
  border-right:0;
  text-align: center;
}
.card-input  .num{
  display: block;
  float: left;
  text-align: center;
  width: 75px;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
}




